.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px ;
    background-color: #fff;
    box-shadow: 0 1px 3px -2px rgba(0, 0, 0, 0.1);
}
.nav-logo img {
    height: 200px !important; 
    width: 200px !important;
}
.nav-logo {
    display: flex;
    align-items: center;
    gap: 10px;
}   
.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #625252;
    font-size: 25px;
    font-weight: 500;

}
.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap:10px;
}
.nav-menu hr {
    border:none;
    width:60%;
    height: 2px;
    border-radius: 2px;
    background-color: #FF4141;
}

.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 45px;
}
.nav-login-cart button {
    padding: 10px 20px;
    background-color: #e52b2b;
    color: white;
    border: none;
    border-radius: 50px;
    cursor: pointer;
    font-size: 16px;
}
.nav-login-cart button:active {
    background-color: #ff4141;
}   

.nav-cart-count {
    position: absolute;
    margin-top: -35px;
    right: 20px;
    background-color: #e11d1d;
    color: white;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: bold;
}